<template>
	<view class="search">
		<music-head title="搜索" :icon="true" color="#000" :iconblack="true"></music-head>
		<view class="container">
			<scroll-view scroll-y="true" >
				<view class="search-search">
					<text class="iconfont icon-search"></text>
					<input type="text" placeholder="搜索歌曲" v-model="searchWord" @confirm="handleToSearch(searchWord)" @input="handleToSuggest"></input>
					<text class="iconfont icon-guanbi" v-if="searchType != 1" @tap="handleToCancel"> </text>
				</view>
				<!-- 搜索前的结构 -->
				<block v-if="searchType == 1">
				<view class="search-history">
					<view class="search-history-head">
							<text>历史记录</text>
							<text class="iconfont icon-lajitong" @tap="handleToClear"></text>
					</view>
					<view class="search-history-list">
						 <view v-for="(item) in searchHistory" @tap="handleToWord(item)">{{item}}</view>
						
					</view>
				</view>
				<view class="search-hot">
					<view class="search-hot-head">
						热搜榜
					</view>
					<view class="search-hot-item" v-for="(item, index) in searchHot" :key="index" @tap="handleToWord(item.searchWord)">
						<view class="search-hot-rate">
							{{index + 1}}
						</view>
						<view class="search-hot-word">
							<view class="">
								{{item.searchWord}} <image :src="item.iconUrl" mode="aspectFit"></image>
							</view>
							<view class="">
								{{item.content}}
							</view>
						</view>
						<text class="search-hot-count">{{item.score}}</text>
					</view>
				</view>
				</block>
				<!-- 搜索后的结构 -->
				<block v-else-if="searchType == 2">
					<view class="search-result">
					<view class="search-result-item" v-for="(item,index) in searchList" :key="index" @tap="handleToDetail(item.id)">
							<view class="search-result-word">
								<view >
									{{item.name}}
								</view>
								<view>
									{{item.artists[0].name}}-{{item.album.name}}
								</view>
							</view>
							<text class="iconfont icon-24gl-playCircle"></text>
						</view>
					</view>
				</block>
				<block v-else-if="searchType == 3">
					<view class="search-suggest">
						<view class="search-head">
							搜索"{{searchWord}}"
						</view>
						<view class="search-suggest-item" v-for="(item, index) in searchSuggest" :key="index" @tap="handleToWord(item.keyword)">
							<text class="iconfont icon-search"></text>
							{{item.keyword}}
						</view>
						
					</view>
				</block>
			</scroll-view>
		</view>
	</view>
</template> 

<script>
	import {searchHot, searchWord, searchSuggest} from '../../common/api.js'
	export default {
		data() {
			return {
				searchHot: [],
				searchWord: '',
				searchHistory: [], 
				searchType: 3,
				searchList: [],
				searchSuggest: []
				
			}
		},
		methods: {
			
			// 点击了热搜榜或者历史记录里的东西, 双向绑定
			handleToWord(word) {
				this.searchWord = word
				this.getSearchList(word)
			},
			
			// 回车搜索了音乐
			handleToSearch(word) {
				console.log(word);
				this.searchHistory.unshift(word)
				this.searchHistory = [...new Set(this.searchHistory)]
				if(this.searchHistory.length > 10) {
					this.searchHistory.length = 10;
				}
				
				// 存入本地存储
				uni.setStorage({
					key: 'searchHistory',
					data: this.searchHistory
				});
				
				// 搜索完触发请求
				this.getSearchList(word)
			}, 
			
			// 清空历史记录
			handleToClear() {uni.clearStorage({
					key: 'searchHistory',
					success: () => {
						this.searchHistory = []
					}
				})
			},
			// 获取搜索完以后的列表
			getSearchList(word) {
				searchWord(word).then((res) => {
					if(res[1].data.code == '200') {
						this.searchList = res[1].data.result.songs;
						this.searchType = 2
					}
					// searchSuggest()
				})
			},
			
			// 点击了关闭
			handleToCancel() {
				console.log(1111);
				this.searchWord = ''
				this.searchType = 1
			},
			
			// 点击了歌曲
			handleToDetail(songId) {
				uni.navigateTo({
					url:'/pages/detail/detail?songId=' + songId
				})
			},
			handleToSuggest(e) {
				let value = e.detail.value;
				if(!value) {
					this.searchType = 1
					return
				}
				searchSuggest(value).then(res =>{
					if(res[1].data.code == '200') {
					this.searchSuggest = res[1].data.result.allMatch;
					this.searchType = 3
					}
				})
			}
			
		}, 
		onLoad() {
			// 获取到热搜的歌曲
			searchHot().then(res => {
				if(res[1].data.code == '200') {
					this.searchHot = res[1].data.data
				}
			}),
			// 获取到本地存储中的历史记录
			uni.getStorage({
				key:'searchHistory', 
				success: ((res)=> {
					this.searchHistory = res.data;
				})
			})
		}
	}
</script>

<style lang="scss" scoped>
	.search-search {
		display: flex;
		height: 70rpx;
		margin: 70rpx 30rpx 50rpx 30rpx;
		align-items: center;
		background-color: #f7f7f7;
		border-radius: 50rpx;
		text {
			font-size: 26rpx;
			margin-right: 26rpx;
			margin-left: 28rpx;
			
		}
		input{
			font-size: 28rpx;
			flex: 1;
			
			
		}
	}
	.search-history {
		margin: 0 30rpx 50rpx 30rpx;
		font-size: 26rpx;
		.search-history-head {
			display: flex;
			justify-content: space-between;
			margin-bottom: 36rpx;
			text {}
		}
		.search-history-list {
			display: flex;
			flex-wrap: wrap;
			view {
				max-width: 700rpx;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				padding: 16rpx 28rpx;
				border-radius: 40rpx;
				margin-right: 30rpx;
				margin-bottom: 30rpx;
				background: #f7f7f7;
			}
		}
	}
	.search-hot {
		margin: 0 30rpx;
		font-size: 26rpx;
		.search-hot-head {
			margin-bottom: 36rpx;
			
			
		}
		.search-hot-item {
			display: flex;
			align-items: center;
			margin-bottom: 58rpx;
			.search-hot-rate {
				color: #fb2222;
				width: 60rpx;
				margin-left: 8rpx;
			}
			.search-hot-word {
				flex: 1;
				view:nth-child(1) {
					font-size: 30rpx;
					color: #000;
				}
				view:nth-child(2) {
					font-size: 24rpx;
					color: #878787;
				}
				image {
					width: 48rpx;
					height: 22rpx;
				}
			}
			text {
				color: #878787;
			}
		}
	}
	.search-result {
		border-top: 2rpx #e4e4e4 solid;
		padding: 30rpx;
		.search-result-item {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding-bottom: 30rpx;
			margin-bottom: 30rpx;
			border-bottom: 2 solid #e4e4e4;
			.search-result-word {
				view:nth-child(1) {
					font-size: 28rpx;
					color: #235790;
					margin-bottom: 12rpx;
				}
				view:nth-child(2) {
					font-size: 22rpx;
					color: #898989;
				}
			}
			text {
			// font-size: 50rpx;
			}
		}
	}
	.search-suggest {
		border-top: 2rpx #e4e4e4 solid;
		padding: 30rpx;
		font-size: 26rpx;
		.search-head{
			color: #4574a5;
			margin-bottom: 74rpx;
		}
		.search-suggest-item {
			margin-bottom: 74rpx;
			color: #5d5d5d;
			text {
					color: #bdbdbd;
					margin-right: 28rpx;
					position: relative;
					top: 2rpx;
			}
		}
	}
</style>
